<template>
    <div class="chart">
        <el-carousel height="600px" :autoplay="false" ref="carousel" class="carousel" indicator-position="none" arrow="always">
            <el-carousel-item>
                <com></com>
            </el-carousel-item>

            <el-carousel-item>
                <el-select v-model="Season" placeholder="请选择">
                    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"> </el-option>
                </el-select>
                <el-select v-model="Country" placeholder="请选择">
                    <el-option v-for="item in Countries" :key="item" :label="item" :value="item"> </el-option>
                </el-select>
                <cdn :Dataset="dataset" :Season="Season" class="charts"></cdn>
            </el-carousel-item>
        </el-carousel>

        <div id="countries" class="countries">
            <div v-for="item in flag" :class="{ flag: true, selected: Country == item.country }" @click="selectCountry(item.country)" :key="'flag-' + item.country">
                <el-avatar :src="item.url" class="avatar"></el-avatar>
                <div>{{ item.country }}</div>
            </div>
        </div>
    </div>
</template>
<script>
    let data = {};
    import com from "@/components/各国奖牌竞争图.vue";
    import cdn from "@/components/各国奖牌折线图.vue";
    export default {
        components: { cdn, com },
        props: ["where"],
        data() {
            return {
                flag: [],
                options: [
                    { value: "Summer", label: "夏季奥运会" },
                    { value: "Winter", label: "冬季奥运会" },
                ],
                dataset: {},
                Season: "Summer",
                Country: "中国",
                Countries: [],
            };
        },
        async mounted() {
            data = await fetch("./各国奖牌排名趋势.json").then((res) => res.json());
            this.Countries = [...new Set([...Object.keys(data.Summer), ...Object.keys(data.Winter)])];
            let flag = await fetch("./国旗地址.json").then((res) => res.json());
            this.flag = Object.entries(flag).map(([key, value]) => {
                return { country: key, url: value };
            });
            this.reDraw();
            switch (this.where) {
                case "各国奖牌折线图":
                    this.$refs.carousel.setActiveItem(1);
                    break;
                case "各国奖牌竞争图":
                    break;
                default:
                    break;
            }
        },
        methods: {
            scrollNext() {
                this.$anime({
                    targets: document.body,
                });
            },
            selectCountry(country) {
                this.$refs.carousel.setActiveItem(1);

                this.Country = country;
            },
            reDraw() {
                this.dataset = { data: data[this.Season][this.Country], Country: this.Country, Season: this.Season };
            },
        },
        watch: {
            Season() {
                this.reDraw();
            },
            Country() {
                this.reDraw();
            },
        },
    };
</script>
<style scoped lang="less">
    .countries {
        user-select: none;
        display: flex;
        flex-direction: row;
        align-items: center;
        overflow-x: scroll;
        width: 100vw;
        height: 100%;
        padding: 10px 0;
        .flag {
            box-shadow: #fcf0f0 -3px -3px 6px, #b4b1b1bf 3px 3px 8px 0px;
            border-radius: 5px;
            font-size: 14px;
            height: 80px;
            padding: 0 5px;
            transition: 0.3s;
            transition-timing-function: ease-in-out;
            margin: 30px 5px 0 5px;
            cursor: pointer;
        }
        .flag:hover {
            margin: 0 20px;
            transform: scale(1.3) translateY(-2px);
        }
        .selected {
            margin: 0 20px;
            transform: scale(1.3) translateY(-2px);
        }
        .avatar {
            background-color: #00000000;
            border-radius: 0;
        }
    }

    .charts {
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .page {
        height: 100vh;
    }
</style>
